@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;400&display=swap);
body {
  margin: 0;
  background-color: beige;
}

h2 {
  margin: 1.2em 0 0 0;
}

p {
  margin: 0.4em 0;
}

input {
  margin: 0;
}

.App {
  padding: 30px;
  display: flex;
  flex-direction: column;
}

.logo {
  margin: 2em auto;
}

h1,
h2 {
  font-family: "Josefin Sans", sans-serif;
}

h1 > a {
  font-size: 1rem;
}

.vertical {
  display: flex;
  margin: auto;
  width: auto;
}

.vertical > div {
  margin: auto;
}

.playbtn {
  position: fixed;
  right: 1rem;
  top: 50%;
  z-index: 100;
  display: flex;
  border-radius: 50%;
  padding: 0.5em 0.5em 0.5em;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
  -webkit-transform-origin: right center;
          transform-origin: right center;
  transition: all 0.05s ease-in-out;
  -webkit-filter: grayscale();
          filter: grayscale();
  background-color: rgba(159, 192, 252, 0.15);
}

.playbtn:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-filter: none;
          filter: none;
}

.center {
  margin: auto;
}

.flex {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.flex-col {
  flex-direction: column;
}

.elegaudio_main {
  position: relative;
}

.elegaudio_overlayCanvas {
  position: absolute;
}

.elegaudio_hoverCanvas {
  opacity: 0.7;
  z-index: 1;
}

.elegaudio_loading {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.eleagudio_disabled {
  -webkit-filter: blur(5px);
          filter: blur(5px);
  pointer-events: none;
}

.range {
  display: flex;
  flex-direction: column;
  align-items: end;
  background-color: white;
  border-radius: 10px;
  padding: 1em;
  margin: auto;
  grid-gap: 0.5em;
  gap: 0.5em;
}

.radio {
  border-radius: 10px;
  background-color: white;
  padding: 1em;
  display: flex;
  flex-direction: column;
  grid-gap: 0.7em;
  gap: 0.7em;
}

.color {
  border-radius: 10px;
  background-color: white;
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.7em;
  gap: 0.7em;
  margin: auto;
}

.radio > div {
  display: flex;
}

.radio > div > input {
  margin-right: 0.4rem;
}

.high {
  transition: all 0.1s;
  opacity: 0.5;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.high:hover {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.inputTitle {
  font-size: 1.2em;
  font-family: "Josefin Sans", sans-serif;
}

.range > label {
  margin-right: 0.4em;
}

label {
  font-family: "Josefin Sans", sans-serif;
}

